import React from 'react';
import {Button,Typography,Avatar,Layout, Menu} from 'antd';
import {Link} from 'react-router-dom';
import {HomeOutlined,BugOutlined,FundOutlined,MoneyCollectOutlined,MenuOutlined, BulbOutlined} from '@ant-design/icons';
import icon from '../images/icon.png';
import './Navbar.css';

const {Title} = Typography
const Navbar=()=>{
    return(
        <div className='nav-container'>
           <div className='logo-container'>
               <Avatar src={icon} size='large' />
               <Typography.Title level={2}>
                   <Link to='/' style={{color:'white'}}>Cryptoverse</Link>
               </Typography.Title>
           </div>
           <Menu mode="inline" theme="dark">
               <Menu.Item key={1} icon={<HomeOutlined/>}>
                 <Link to='/'>Home</Link>
               </Menu.Item>
               <Menu.Item key={2} icon={<FundOutlined/>}>
                 <Link to='/cryptocurrencies'>Cryptocurrencies</Link>
               </Menu.Item>
               <Menu.Item key={3} icon={<MoneyCollectOutlined/>}>
                 <Link to='/exchanges'>Exchanges</Link>
               </Menu.Item>
               <Menu.Item key={4} icon={<BulbOutlined/>}>
                 <Link to='/news'>News</Link>
               </Menu.Item>
           </Menu>
        </div>
    )
}
export default Navbar;