import React,{useState, useEffect} from 'react';
import {Card,Col,Row,Input} from 'antd';
import {useGetCryptosQuery} from '../services/cryptoApi';
import {Link} from 'react-router-dom';
import Homepage from './Homepage'
import '../App.css';
import axios from 'axios';

const Cryptocurrencies = ({simplified}) => {
    if(simplified===true){
        simplified=false
    }
    else{
        simplified=true;
    }
    const {data:cryptoList,isFetching}=useGetCryptosQuery();
    const [cryptos,setCryptos] = useState([]);
    const [search, setSearch] = useState('');
    console.log(cryptos);
    console.log(cryptoList);


    
    useEffect(() => {
       
        
        const getCrypto = async()=>{
                let filterData = cryptoList?.data?.coins.filter((coin)=>coin.name.toLowerCase().includes(search.toLowerCase()))
                setCryptos(filterData);
                
           
        }
        getCrypto();
    
    },[cryptoList,search]);
    
        const searchField =(event)=>{
            setSearch(event.target.value);
    
        }
    
     
        if(isFetching) return 'loading...';
    

   
   
    

  
    
    return (
        <>
         {simplified?<Input placeholder='search here' style={{width:'30%',marginLeft:'35%',marginTop:'20px'}}
         onChange={searchField} />:''}
        <div className="site-card-wrapper" style={{padding:'20px 20px'}}>
        
    <Row gutter={[32,32]}>
    {

      cryptos?.map((currency,i)=>(
        simplified?(
            
        <Col span={8} xs={24} sm={12} lg={6} key={currency.name}>
        <Link to={`/crypto/${currency.uuid}`}>
        {console.log(currency.id)}
        <Card className='cards' title={`${currency.rank}.${currency.name}`} bordered={false} 
        extra={<img src={currency.iconUrl} width='25px' />}
       hoverable >
          <p>Price :{currency.price}</p>
          <p>Market Cap:{currency.marketCap}</p>
          <p>Daily Changes:{currency.change}</p>
        </Card>
        </Link>
      </Col>)
      :
      
      (
         i<10?(
        <Col span={8} xs={24} sm={12} lg={6} key={currency.name}>
        <Link to={`/crypto/${currency.uuid}`}>
        <Card className='cards' title={`${currency.rank}.${currency.name}`} bordered={false} 
        extra={<img src={currency.iconUrl} width='25px' />}
       hoverable >
          <p>Price :{currency.price}</p>
          <p>Market Cap:{currency.marketCap}</p>
          <p>Daily Changes:{currency.change}</p>
        </Card>
        </Link>
      </Col>
         ):''
      )
      ))
   
    }
    </Row>
  </div>
  </>
    )
}

export default Cryptocurrencies
