import React,{useState,useEffect} from 'react';
import {useGetcryptoNewsQuery} from '../services/cryptoNewsApi';
import {Card,Row,Col,Typography,Select} from 'antd';
import moment from 'moment';
import {useGetCryptosQuery} from '../services/cryptoApi'

const {Option} = Select;

const { Title } = Typography;
const News = ({x}) => {
    if(x===true){
        x=false
    }
    else{
        x=true
    }
  
    const [category,setCategory] = useState('Cryptocurrency')
    const [newsCategory,setNewscategory]=useState([]);
    const {data,isFetching} = useGetcryptoNewsQuery({categories:category});
    const {data:nameofcoins} = useGetCryptosQuery();
    console.log(nameofcoins?.data?.coins[0]?.name)
    console.log(data)
    useEffect(()=>{
        const getNews=async()=>{
            const newsData = await data?.value;
            setNewscategory(newsData);
        }
        getNews();
    },[data]);
    function handleChange(value) {
        setCategory(value)
      }
    if(!data?.value) return 'loading...';
    // console.log(data.value);


    return (
    <div style={{padding:'20px'}}>
          <div className="site-card-wrapper">
    <Select defaultValue="Cryptocurrency" style={{ width: 120 }} onChange={handleChange}>
    {nameofcoins?.data?.coins?.map((coin)=>(
        <Option key={coin.name} value={coin.name}>{coin.name}</Option>
    ))}
      
     
    </Select>
    <Row gutter={[16,16]}>
      {newsCategory?.map((item,i)=>(
        
        x?     
    <Col span={8} xs={24} sm={12} lg={8} span={8} key={item.name}>
     <Card 
     style={{minHeight:'280px'
     // ,backgroundImage: `url(${item.image.thumbnail.contentUrl})`
      }} bordered={false} 
      hoverable
     >
     <a href={item.url} style={{color:'black'}}>
     <Title  level={3}>{item.name}</Title>
     <p>{item.description.length>100?item.description.substring(0,100)+'...':item.description}</p>
     <div>
     <div style={{display:'inline-block'}}>
         <img style={{width:'25px'}} src={item.provider[0]?.image?.thumbnail?.contentUrl || 'https://images.financialexpress.com/2021/05/crypto.jpg'}/>
         {' '}{item.provider[0].name}
     </div>
     <div style={{float:'right',display:'inline-block'}}>
     {moment(item.datePublished).startOf('ss').fromNow()}
     </div>
     </div>
     
     </a>

     </Card>
   
   </Col>
          
            :(i<6?
                <Col span={8} xs={24} sm={12} lg={8} span={8} key={item.name}>
     
     <Card 
     style={{minHeight:'280px'
     // ,backgroundImage: `url(${item.image.thumbnail.contentUrl})`
      }} bordered={false} 
      hoverable
     >
     <a href={item.url} style={{color:'black'}}>
     <Title  level={3}>{item.name}</Title>
     <p>{item.description.length>100?item.description.substring(0,100)+'...':item.description}</p>
     <div>
     <div style={{display:'inline-block'}}>
         <img style={{width:'25px'}} src={item.provider[0]?.image?.thumbnail?.contentUrl || 'https://images.financialexpress.com/2021/05/crypto.jpg'}/>
         {' '}{item.provider[0].name}
     </div>
     <div style={{float:'right',display:'inline-block'}}>
     {moment(item.datePublished).startOf('ss').fromNow()}
     </div>
     </div>
     </a>
     </Card>
   
   </Col>:''
            )    
            
           
        
      ))}
      
    </Row>
  </div>
  </div>
    )
}

export default News
